import { Component, Input, OnInit } from '@angular/core';
import { IOptionsPsDropdownSukukTypesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-sukuk-type/ps-dropdown-sukuk-type.component.interface';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsSelect } from 'src/app/commonSRC/psServices/models/common-type';
import { IOptionsPsSelectDropdown } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

@Component({
  selector: 'ps-dropdown-sukuk-type',
  templateUrl: './ps-dropdown-sukuk-type.component.html',
  styleUrls: ['./ps-dropdown-sukuk-type.component.scss'],
})
export class PsDropdownSukukTypeComponent extends PsSelectDropdownComponent implements OnInit {
  @Input() options: IOptionsPsDropdownSukukTypesExposed;
  defaultSelectOptions: IOptionsPsSelectDropdown = {
    labelKey: 'sukuk_type_key',
    placeHolder: 'select_sukuk_type_key'
  };
  public sukukType: PsSelect = [];
  constructor(commonProv: PsCommonService, loggerP: LoggerService, private omniPull: OmniPullService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options);
    this.defaultSelectOptions.asyncURL = PsCommonBusinessSettings.serviceUrl.sukukTypes;
    this.defaultSelectOptions.serviceRequestObj = {};
    this.defaultSelectOptions.mapResponseProperties = { itemValue: "code", description: 'briefNameEnglish' }
    //this.getSukukTypes();
  }

  getSukukTypes() {
    const paramData: any = {};
    this.omniPull.returnSukukTypes(paramData).then((result) => {

      if (result && result.gridModel != null && result.gridModel.length > 0) {
        for (let i = 0; i < result.gridModel.length; i++) {
          const sukukjtype = {
            itemValue: result.gridModel[i].code,
            description: result.gridModel[i].briefNameEnglish, selectedObj: result.gridModel[i]
          };
          this.sukukType.push(sukukjtype);
        }
        this.defaultSelectOptions.listOfOptions = this.sukukType;
      } else { this.logger.warn(result, 'empty response'); }
    }).catch((error) => { });
  }
  onSukukTypeChange(values: any) {
    this.loggerP.log('onSukukTypeChange' + values);
    this.onPsChange.emit(values.selectedObj);
  }
}
