import { Component, Input, OnInit } from '@angular/core';
import { PsSelectDropdownComponent } from './../../../../commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from './../../../../commonSRC/psServices/logger/logger.service';
import { PsSelect } from './../../../../commonSRC/psServices/models/common-type';
import { IOptionsPsSelectDropdown } from './../../../../commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from './../../../../commonSRC/psServices/ps-common/ps-common.service';
import { OmniPullService } from './../../../psServices/omni-common/omni-pull.service';
import { IOptionsPsDropdownBillerIntegratedExposed } from './ps-dropdown-biller-integrated.component.interface';

@Component({
    selector: 'ps-dropdown-biller-integrated',
    templateUrl: './ps-dropdown-biller-integrated.component.html',
})
export class PsDropdownBillerIntegratedComponent extends PsSelectDropdownComponent implements OnInit {
    @Input() options: IOptionsPsDropdownBillerIntegratedExposed;
    defaultSelectOptions: IOptionsPsSelectDropdown = {
        labelKey: 'biller_key',
        placeHolder: 'choose_biller_key',
        mapResponseProperties: { itemValue: 'billerCode', description: 'cifBillerName', iconName: 'providerImage' },
        enablePagination: false,
    };
    public billerTypes: PsSelect = [];
    constructor(commonProv: PsCommonService, loggerP: LoggerService, private omniPull: OmniPullService) {
        super(commonProv, loggerP);
    }

    ngOnInit() {
        this.commonProv.copyObject(this.defaultSelectOptions, this.options);
    }

    onTypeChange(values: any) {
        this.onPsChange.emit(values);
    }

}

