import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { IFileFormat} from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { FileService } from '../../../commonSRC/psServices/Files/file.Service';
import { IOptionsActionSheet, IOptionsPsActionButton } from '../../../commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from '../../../commonSRC/psServices/ps-common/ps-common.service';
import { PsActionButtonComponent } from '../../../commonSRC/psComponents/ps-base/ps-base-action/ps-action-button/ps-action-button.component';
import { IOptionsPsButtonExport } from './ps-button-export.component.interface';
import { OmniCommonService } from '../../psServices/omni-common/omni-common.service';


@Component({
  selector: 'ps-button-export',
  templateUrl: './ps-button-export.component.html',
  styleUrls: ['./ps-button-export.component.scss'],
})
export class PsButtonExportComponent extends PsActionButtonComponent implements OnInit {

  @Input() options: IOptionsPsButtonExport;
  exportOptions: IOptionsPsActionButton;

  constructor(private commonService: PsCommonService,private omniCommonService: OmniCommonService, private fileService: FileService, elRef?: ElementRef) {
    super(commonService, commonService.logger, elRef);
  }

  ngOnInit() {
    this.exportOptions = {
      labelKey: this.options.labelKey ? this.options.labelKey : 'export_key',
      type: 'button',
      iconName: 'download',
      psClass: 'ps-button-export',
      group: this.options.group
    };

  }

  onExportClicked(event) {
    const actionSheetOptions: IOptionsActionSheet = {
      header: this.common.translate('select_an_option_key'),
      buttons: [{
        cssClass: 'export_pdf ' + 'export_pdf_' + PsCommonSettings.oper_ID,
        text: this.common.translate('pdf_key'),
        handler: () => {
          this.omniCommonService.exportData(IFileFormat.PDF, this.options.reportParametersList);
        }
      },
      {
        cssClass: 'export_excel ' + 'export_excel_' + PsCommonSettings.oper_ID,
        text: this.common.translate('excel_key'),
        handler: () => {
          this.omniCommonService.exportData(IFileFormat.EXCEL, this.options.reportParametersList);
        }
      },
      {
        cssClass: 'cancel_export ' + 'cancel_export_' + PsCommonSettings.oper_ID,
        text: this.common.translate('cancel_key'),
        role: 'cancel'
      }]
    };
    this.commonService.presentActionSheet(actionSheetOptions);
  }
}
