import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsSelectDropdown, IPsSelect } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from './../../../../commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownAllOperationsListExposed } from './ps-dropdown-all-operations-list.component.interfaces';

/**
 * @author G.Radwan
 * @since 26/04/2021
 *
 * <p> PsDropdownAllOperationsList will fetch the list of all operations from server</p>
 */
@Component({
  selector: 'ps-dropdown-all-operations-list',
  templateUrl: './ps-dropdown-all-operations-list.component.html',
  styleUrls: ['./ps-dropdown-all-operations-list.component.scss'],
})
export class PsDropdownAllOperationsList extends PsSelectDropdownComponent implements OnInit {

  @Input() options: IOptionsPsDropdownAllOperationsListExposed = {};
  public defaultOptions: IOptionsPsSelectDropdown = {};

  constructor(
    commonService: PsCommonService,
    logger: LoggerService,
    private omniPullService?: OmniPullService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    this.handelDropDownInfo();
    this.loadOperationsList();
  }

  /**
   * populating operations list fetched from server
   */
  private async loadOperationsList() {

    const operationsList = await this.omniPullService.returnOperationsList({}).catch(error => {
      this.logger.error('Error: While fetching Facility types in PsDropdownAllOperationsList : ', error);
    });

    if (operationsList && operationsList.gridModel && operationsList.gridModel.length > 0) {
      this.defaultOptions.listOfOptions = [];
      for (const iterator of operationsList.gridModel) {
        const FacilityType: IPsSelect = {
          itemValue: iterator.itemValue,
          description: iterator.description,
          selectedObj: iterator
        };
        this.defaultOptions.listOfOptions.push(FacilityType);
      }
    }else{
      this.defaultOptions.listOfOptions = [];
    }
  }

  selectDropDownChange(values: IchangeValues) {
    this.onPsChange.emit(values);
  }

  handelDropDownInfo() {
    if (PsCommonSettings.oper_ID === CommonBussinessConstant.ACTIVITY_LOG_OPER_ID) {
      this.defaultOptions.labelKey = this.common.translate('activity_key');
      this.defaultOptions.placeHolder = this.common.translate('select_activity_key');
    } else {
      this.defaultOptions.labelKey = this.common.translate('transaction_identification_key');
      this.defaultOptions.placeHolder = this.common.translate('select_transaction_identification_key');
    }
  }
}
